@import "_variables";

* {
  padding: 0;
  margin: 0;
}

// General
body {
  margin: 0;
  background: $color-bg;
  color: $color-black;
  scroll-behavior: smooth;
}

// Typography
h2 {
  font-size: 24px;
  font-weight: 600;
  line-height: 1.4;
  margin-bottom: 1em;
}
p {
  font-size: 15px;
  font-weight: 300;
  line-height: 1.6;
  margin-bottom: 1em;
}

//Link
a,
a:hover,
a:active,
a:focus,
a:visited {
  text-decoration: none;
}
