@import "_variables";

.contact-wrapper {
  box-sizing: border-box;
  margin-top: 2px;

  ul {
    align-items: center;
  }

  li {
    float: left;
    width: 24px;
    color: $color-main;
    padding: 0px 10px 0px 10px;

    svg {
      path {
        fill: $color-main;
        transition: fill 0.125s ease;
      }
    }

    &:hover {
      cursor: pointer;
      //margin-top: 2px;
      transform: scale(1.2);
      svg {
        path {
          fill: $color-secondary !important;
        }
      }
    }
  }
}
