@import "_variables";

@mixin mq($width) {
  @media screen and (max-width: $width) {
    @content;
  }
}

.project-wrapper {
  width: 100vw;
  height: 100vh;
  max-width: 100%;
  font-family: "Roboto Mono", monospace;
  display: flex;
  overflow: hidden;

  @include mq(980px) {
    left: 0;
    width: 100vw;
    flex-direction: column;
  }

  // left //
  .project-detail {
    left: 12vw;
    width: 38vw;
    position: relative;

    @include mq(980px) {
      left: 0;
      width: 100vw;
      height: 40vh;
      padding: 10vw 10vw;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
    }

    .number {
      z-index: -2;
      position: absolute;
      top: 5vh;
      right: 0;
      font-size: 10vw;
      font-family: "Abril Fatface", "Noto Sans TC", sans-serif;
      color: $color-main;
      opacity: 0.4;

      @include mq(980px) {
        opacity: 0;
      }
    }

    .detail-wrapper {
      position: absolute;
      bottom: 40vh;
      box-sizing: border-box;
      flex-direction: column;

      @include mq(980px) {
        bottom: 10vh;
      }

      .title {
        width: 35vw;
        height: auto;
        margin-bottom: 3vh;
        font-size: 5vw;
        font-family: "Abril Fatface", "Noto Sans TC", sans-serif;
        color: $color-secondary;
        word-wrap: break-word;

        @include mq(980px) {
          width: 50vw;
          font-size: 12vw;
        }
      }

      .description {
        position: absolute;
        background-color: $color-white;
        color: $color-black;
        padding: 2vw 8vw 2vw 2vw;
        width: 34vw;
        height: 25vh;
        font-size: 1vw;
        line-height: 1.8;
        word-wrap: break-word;

        @include mq(980px) {
          width: 80vw;
          height: 25vh;
          font-size: 2vw;
          padding: 2vw 8vw 2vw 2vw;
          box-sizing: border-box;
          display: flex;
        }

        .arrow-button {
          position: absolute;
          top: 50%;
          right: 2.5vw;
          height: 20px;
          width: 20px;
          border: {
            top: 2px solid $color-secondary;
            left: 2px solid $color-secondary;
          }
          transform: rotate(135deg) translateX(-25%) translateY(25%);
          transition: all 0.2s ease-in-out;

          &:hover {
            border: {
              top: 4px solid $color-secondary;
              left: 4px solid $color-secondary;
            }
          }
        }
      }
    }
  }

  // right //
  .image-wrapper {
    z-index: -1;
    left: 12vw;
    width: 50vw;
    position: relative;
    background-size: cover;
    background-position: center;
    overflow: hidden;

    @include mq(980px) {
      left: 0;
      width: 100vw;
      height: 60vh;
    }

    &:after,
    &:before {
      content: "";
      width: 150vw;
      height: 150vw;
      border-radius: 40%;

      @include mq(980px) {
        width: 200vh;
        height: 200vh;
      }
    }

    &:after {
      background-color: $color-secondary;

      position: absolute;
      top: 0vh;
      left: -30vw;
      display: block;
      z-index: -1;

      animation: wave 12s cubic-bezier(0.36, 0.45, 0.63, 0.53) infinite;
      transition: 0.5s;

      @include mq(980px) {
        top: -10vh;
      }
    }

    &:before {
      background-color: $color-secbg;

      position: absolute;
      top: 20vh;
      left: -50vw;
      display: block;

      animation: wave 12s cubic-bezier(0.36, 0.45, 0.63, 0.53) infinite;
      transition: 0.5s;

      @include mq(980px) {
        top: 10vh;
      }
    }

    .project-image {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 40vw;
      height: 60vh;
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
      transition: 0.2s ease-in-out;
      filter: drop-shadow(0px 0px 5px rgba(0, 0, 0, 0.3));

      @include mq(980px) {
        width: 80vw;
        height: 40vh;
        bottom: -15vh;
      }
    }
    &:hover {
      transform: scale(1.2);
      transition: 0.2s ease-in-out;
    }
  }
}

@keyframes wave {
  0% {
    transform: translate(-15%, 10%) rotate(0deg);
  }
  100% {
    transform: translate(-15%, 10%) rotate(360deg);
  }
}
