@import "_variables";

.hamburger-menu {
  div {
    position: relative;
    width: 28px;
    height: 4px;
    background-color: $color-main;
    margin-bottom: 4px;
    transition: all 0.4s ease-in-out;
  }
}

#menu-toggle {
  display: none;
}

#menu-toggle:checked + .hamburger-menu {
  .top {
    transform: rotate(45deg);
    background-color: $color-white;
    margin-top: 10px;
  }
  .meat {
    transform: scale(0);
  }
  .bottom {
    transform: rotate(-45deg);
    background-color: $color-white;
    margin-top: -16px;
  }
  & + .nav {
    top: 0;
  }
}

.nav {
  z-index: 8;
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: $color-secondary;
  opacity: 0.9;
  top: -100%;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  transition: all 0.3s ease-in-out;

  .nav-wrapper {
    position: relative;
    overflow-y: auto;
    height: 100%;
  }
}

.menu-links {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .link {
    margin-top: 10px;
    color: $color-white;
    opacity: 1;
    text-decoration: none;
    font-size: 2.3em;
    letter-spacing: 3px;
    transition: all 0.1s ease;

    &:first-child {
      margin-top: 0;
    }
  }
}

#menu-toggle:checked + .hamburger-menu + .nav {
  .nav-wrapper nav .link {
    transform: scale(1);

    &:hover {
      color: $color-white;
      transform: scale(1.2);
    }
  }
}
