@import "_variables";

.logo {
  transition: margin 0.125s ease;

  svg {
    transform: scale(0.35);
    transform-origin: 0 0;
    polygon,
    rect {
      fill: $color-main;
      transition: fill 0.125s ease;
    }
  }

  &:hover {
    cursor: pointer;
    margin-top: 2px;
    svg {
      polygon,
      rect,
      path {
        fill: $color-secondary !important;
      }
    }
  }
}
