@import "_variables";

@mixin mq($width) {
  @media screen and (max-width: $width) {
    @content;
  }
}

.profile {
  width: 50vw;
  padding: 8vw 10vw;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  color: $color-black;

  @include mq(980px) {
    width: 100vw;
    height: 40vh;
  }

  .name {
    font-size: 6vw;
    font-family: "Abril Fatface";
    margin-bottom: 4vw;
    color: $color-secondary;
  }

  .biography {
    font-size: 14px;
    line-height: 1.8;
    margin-bottom: 3vw;
    opacity: 0.8;

    a {
      color: $color-secondary;
      &:hover {
        color: $color-main;
      }
    }
  }
}
