@import "_variables";

@mixin mq($width) {
  @media screen and (max-width: $width) {
    @content;
  }
}

.header {
  width: 12vw;
  height: 100vh;
  display: flex;
  position: fixed;
  box-sizing: border-box;
  top: 0;

  @include mq(980px) {
    width: 100vw;
    height: auto;
  }

  .hamburger-menu {
    z-index: 10;
    position: absolute;
    width: 30px;
    height: 30px;
    top: 6vh;
    left: 50%;
    transform: translateX(-50%);

    @include mq(980px) {
      left: 10%;
    }
  }

  .logo {
    z-index: 5;
    position: absolute;
    width: 30px;
    height: 30px;
    bottom: 6vh;
    right: 50%;
    transform: translateX(50%);

    @include mq(980px) {
      top: 6vh;
      right: 10%;
    }
  }

  .web-title {
    z-index: 5;
    position: absolute;
    margin: 0 auto;
    width: 150px;
    height: 30px;
    top: 50%;
    left: 50%;
    transform: rotate(-90deg) translateX(10%) translateY(-225%);

    color: $color-main;
    font-family: "Montserrat";
    text-align: center;

    &:hover {
      color: $color-secondary;
    }

    @include mq(980px) {
      display: none;
      //left: 50%;
      //transform: rotate(0deg) translateX(-25%);
      //top: 7%;
    }
  }
}
