@import "_variables";

@mixin mq($width) {
	@media screen and (max-width: $width) {
		@content;
	}
}

.Cursor {
    position: absolute;
    height: 40px;
	width: 40px;
	border: 1px solid $color-black;
	border-radius: 50%;
	box-sizing: border-box;
	transition: 0.1s;
	transform: translate(-50%, -50%);
	pointer-events: none;
	z-index: 99998;

	@include mq(980px) {
        width: 0px;
		height: 0px;
		border: none;
    }
}
