@import "_variables";

@mixin mq($width) {
  @media screen and (max-width: $width) {
    @content;
  }
}

.arrow-box {
  display: flex;
  position: fixed;
  bottom: 50%;
  right: 3vw;

  @include mq(980px) {
    transform: rotate(-90deg);
    right: 50%;
    bottom: 6vh;
    display: inline-block;
  }

  .arrow-up,
  .arrow-down {
    height: 20px;
    width: 20px;
    border: {
      top: 2px solid $color-bg;
      left: 2px solid $color-bg;
    }

    &:hover {
      border: {
        top: 2px solid $color-white;
        left: 2px solid $color-white;
      }
    }
  }
}

.arrow-up {
  position: absolute;
  transform: rotate(45deg);
  bottom: 30px;
  right: 0;
}

.mid {
  position: absolute;
  width: 28px;
  border-top: 2px solid $color-bg;
  right: -3px;
}

.arrow-down {
  position: absolute;
  transform: rotate(225deg);
  top: 30px;
  right: 0;
}
